<template>
  <div class="search">
    <van-nav-bar title="搜索中心" left-text="返回" left-arrow @click-left="goback" />
    <van-search
      v-model="keywords"
      shape="round"
      background="rgb(249,187,50)"
      input-align="center"
      placeholder="请输入搜索关键词"
      show-action
      @search="onSearch"
    >
      <template #action>
        <div @click="onSearch">搜索</div>
      </template>
    </van-search>
    <div class="product-box" v-if="this.searchData.length>0">
      <!--  :class="[index % 2 == 0 ? 'p-left' : 'p-right']"-->
      <div class="product-item" v-for="(item, index) in searchData" :key="index">
        <div class="p-img" @click="goDetail(item.pid)">
          <img class="auto-img" :src="item.smallImg" alt />
        </div>

        <div class="pro-name one-text">{{item.name}}</div>
        <div class="prices">
          <div class="pro-price">￥{{item.price}}</div>
          <!-- <div
                :class="[item.isLike?'iconfont iconshoucang-copy': 'iconfont  iconshoucang']"
                @click="like(item,index)"
          ></div>-->
        </div>
      </div>
    </div>
    <div v-else>
      <h3>暂无搜索的商品</h3>
    </div>
  </div>
</template>

<script>
import '../../assets/css/search.less'
export default {
  data() {
    return {
      keywords: "",
      searchData: [],
    };
  },
  created() {
    this.keywords = this.$route.query.keywords;
    // 
    this.getProduct(this.keywords);
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    //获取关键词数据
    getProduct(keywords) {
      
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "/search",
        //如果get请求，参数需要放在params, 如果是post请求,参数需要放在data
        params: {
          appkey: this.appkey,
          name: this.keywords,
        },
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == "Q001") {
            result.data.result.forEach((v) => {
              
              if (v.name.includes(keywords)) {
                this.searchData.push(v);
              } else {
                this.$toast("暂时无搜索的商品");
              }
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //进入商品详情
    goDetail(pid) {
      this.$router.push({ name: "Details", query: { pid } });
    },
    onSearch(keywords) {
      if(keywords==''){
        return;
      }
      this.keywords = keywords;
      this.$router.push({ name: "Search", query: { keywords } });
      this.getProduct(this.keywords);
    },
  },
};
</script>

<style lang="less" scoped>

</style>